import React, {Component } from "react";
import Banner from "../banner";
class Home extends Component {
    render () {
        return (
            <div>
            <Banner imageUrl="https://res.cloudinary.com/vierbit/image/upload/v1529749619/slide2.jpg" />
            <main style={{"background":"url(images/xtile1.png.pagespeed.ic.dDV6jr4IDU.png)"}}>
            <div className="container">
                <div className="parsys main-content-area">
                    <div className="title-component section">
                        <h1>
                            Thairpy
                            <span>Spa &amp; Salon at Edmonton</span>
                        </h1>
                    </div>
                    <div className="richtext full-width-text section">
                        <div className="text">
                            <p>Edmonton Hair Salon, that offers exceptional spa and salon services in a luxurious and relaxing environment that is second to none! Our master stylist and senior estheticians have extensive knowledge in their fields with many years of experience and are Keune coloring line-certified and members of the Unite Professional Salon System. Our services include cuts, colors , waxing, manicures, pedicures, laser hair removal, and much more. Call Thairpy Salon and Spa today 780-484-3555 to schedule your appointment.&nbsp;</p>
                        </div>
                    </div>
                </div>
                <div className="parsys main-content-area">
                    <div className="title-component section">
                        <h1>
                            Hours
                        </h1>
                    </div>
                    <div className="richtext full-width-text section">
                        <div className="text">
                            <p style={{"textAlign": "center"}}>
                                <b>&nbsp;10:00 am - 7:00 pm&nbsp;</b>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </main>
        </div>
        );
    }
}

export default Home;


